.nav-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-bottom: 40px;
}

  
  .nav-item {
    width: 400px;
    text-align: center; /* Center the content */
    margin: 0 20px; /* Add 20px margin to the left and right of each item */
  }
  
  .nav-item img {
    width: 400px; /* Control the image size */
    height: auto; /* Maintain aspect ratio */
  }
  
  /* Optional: Remove padding/margin from <ul> if you keep it */
  ul {
    margin: 0;
    padding: 0;
  }
  
  .site-footer {
    background-color: #ecf1fb;
    text-align: center;
    font-size: 14px;
    color: #777;
    padding: 20px 10px;
  }

  .section-title {
    text-align: center;
    font-size: 24px;
    margin: 40px 0 20px 0;
    color: #333;
  }
  
  .nav-placeholder {
    display: inline-block;
    width: 400px;
    height: 225px;
    background-color: #f0f0f0;
    border-radius: 12px;
    line-height: 225px;
    text-align: center;
    font-size: 20px;
    color: #333;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .recruiter-blurb {
    max-width: 800px;
    margin: 0 auto 30px auto;
    padding: 0 20px;
    text-align: center;
    font-size: 16px;
    color: #444;
    line-height: 1.6;
  }
  