/* blog-post.css */
.blog-post {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    line-height: 1.6;
  }
  
  .blog-post h2 {
    font-size: 24px;
    margin-top: 40px;
    margin-bottom: 20px;
  }
  
  .blog-post p {
    margin-bottom: 20px;
  }
  
  .blog-post code {
    background-color: #f0f0f0;
    padding: 2px 4px;
    font-family: 'Courier New', monospace;
  }

  .formula-container {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .formula {
    font-size: 18px;
    font-weight: bold;
    background-color: #f0f0f0;
    padding: 10px;
    border-radius: 5px;
  }
  
  .formula sup {
    font-size: 12px;
  }

  .video-container {
    margin: 20px auto;
    display: flex;
    justify-content: center;
  }
  
  .transcript {
    max-width: 800px;
    margin: 0 auto;
    text-align: left;
    padding: 20px;
    font-size: 16px;
    line-height: 1.6;
  }
  
  @media (max-width: 768px) {
    .video-container {
      margin-top: 60px; /* or more depending on your arrow size */
    }
  }
  